<template>
  <div class="team-page">
    <div v-if='inviteVisible' class="invite flex ac js" @click="invite">
      <img :src="require('@/assets/invite.png')" alt />
      <div class="item">邀请新成员加入团队</div>
      <img :src="require('@/assets/right.png')" alt />
    </div>
    <Notice v-if="count && taskVisible" class="box" :text="text" @click.native="toOther('/task')"></Notice>
    <div class="mine box flex ac">
      <CustomImg class="portait" width="1.33rem" height="1.33rem" :src="self.head_pic" round></CustomImg>
      <div class="item">
        <div class="flex ac nickname">
          {{self.nickname | longtext}}
          <img :src="require('@/assets/me.png')" />
        </div>
        <div class="mine-id">会员ID：{{self.invite_code}}</div>
      </div>
      <div class="copy flex ac center" :data-clipboard-text="self.invite_code" @click="copy">
        <img :src="require('@/assets/copy.png')" />
        复制ID
      </div>
    </div>
    <div class="box list">
      <div class="list-title">团队成员（{{total}}）</div>
      <InfiniteList
        ref="list"
        v-slot="slotProps"
        api="teamManage/team"
        :formatData="formatData"
        immediateCheck
      >
        <div>
          <div
            v-for="item in slotProps.list"
            :key="item.user_id"
            class="list-item flex ac js"
            @click="toOther(`/team/info?user_id=${item.user_id}&nickname=${item.nickname}&head_pic=${item.head_pic}`)"
          >
            <CustomImg width="1.33rem" height="1.33rem" :src="item.head_pic" round></CustomImg>
            <div class="item flex ac nickname list-center">
              {{item.nickname | longtext}}
              <span class="tag ellipsis">
                {{item.level_name}}
                <img class="diamond" :src="require('@/assets/diamond.png')" />
              </span>
            </div>
            <div class="btn">查看详情</div>
            <div v-if="item.wait_auit && taskVisible" class="wait">任务待审核</div>
          </div>
        </div>
      </InfiniteList>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ClipboardJS from "clipboard";
import { getTaskCount } from "@/api";
import Notice from "@/components/Notice";
import InfiniteList from "@/components/InfiniteList";

export default {
  components: { Notice, InfiniteList },
  data() {
    return {
      count: 0, // 任务数量
      total: 0, // 团员数量
      self: {} // 我的数据
    };
  },
  computed: {
    ...mapGetters({taskVisible: 'config/taskVisible'}),
    ...mapGetters({inviteVisible: 'config/inviteVisible'}),
    text() {
      return `您有${this.count}个待审核任务未处理`;
    }
  },
  created() {
    this.getCount();
    this.$store.dispatch("config/getConfig");
  },
  methods: {
    // 待审核任务数量
    getCount() {
      getTaskCount().then(res => {
        if (res.success) {
          this.count = res.data.count;
        }
      });
    },
    // 跳转
    toOther(path) {
      this.$router.push(path);
    },
    // 数据格式化
    formatData(res) {
      const { self, list } = res;
      const { data, last_page, current_page, total } = list;
      this.self = self;
      this.total = total;
      return {
        data,
        last_page,
        current_page
      };
    },
    // 复制
    copy() {
      console.log("copy");
      const clipboard = new ClipboardJS(".copy");
      clipboard.on("success", e => {
        this.$toast("复制成功");
        e.clearSelection();
      });
      clipboard.on("error", () => {
        this.$toast("复制失败");
      });
    },
    invite() {
      const invite_code = JSON.parse(localStorage.getItem("userInfo"))
        .invite_code;
      const url = `${process.env.VUE_APP_CHUNGOU_H5}/goodsList?invite_code=${invite_code}`;
      location.href = url;
    }
  }
};
</script>

<style lang="less" scoped>
.team-page {
  padding-bottom: 72px;
  padding-bottom: calc(72px + constant(safe-area-inset-bottom));
  padding-bottom: calc(72px + env(safe-area-inset-bottom));
}

.invite {
  background: #fff;
  padding: 0 14px;
  color: #282828;
  height: 44px;
  font-size: 14px;
  img:first-child {
    width: 18px;
  }
  img:last-child {
    width: 6px;
  }
  div {
    padding: 0 10px;
  }
}

.box {
  margin: 10px 13px;
}

.mine {
  background: #fff;
  border-radius: 8px;
}

.portait {
  margin: 15px 13px;
}

.nickname {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  img {
    width: 18px;
    margin-left: 5px;
  }
}

.mine-id {
  color: #777;
  font-size: 12px;
  line-height: 17px;
  margin-top: 5px;
}

.copy {
  width: 60px;
  height: 23px;
  background: rgba(252, 149, 0, 1);
  border-radius: 12px 0px 0px 12px;
  box-sizing: border-box;
  color: #fff;
  font-size: 12px;
  img {
    width: 13px;
    margin-right: 5px;
  }
}

.list {
  background: #fff;
  border-radius: 8px;
  padding: 11px 10px 5px;
}

.list-title {
  color: #282828;
  font-size: 16px;
  line-height: 23px;
}

.list-item {
  padding: 15px 0;
  border-bottom: 1px solid #d8d8d8;
  position: relative;
  &:last-child {
    border-bottom: none;
  }
}

.list-center {
  padding-left: 10px;
}

.wait {
  width: 60px;
  height: 14px;
  line-height: 14px;
  font-size: 10px;
  text-align: center;
  background: rgba(232, 65, 20, 1);
  border-radius: 80px 50px 50px 0px;
  color: #fff;
  position: absolute;
  top: 15px;
  left: 28px;
}

.tag {
  max-width: 99px;
  box-sizing: border-box;
  margin-left: 8px;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
  padding: 1px 5px 1px 17px;
  background: rgba(255, 197, 2, 1);
  border-radius: 30px 30px 30px 30px;
  position: relative;
  line-height: 1;
  img {
    width: 17px;
    position: absolute;
    top: 50%;
    left: -1px;
    transform: translateY(-50%);
    margin-left: 0;
    z-index: 1;
  }
}

.btn {
  width: 60px;
  height: 20px;
  border-radius: 10px;
  border: 1px solid rgba(238, 238, 238, 1);
  color: #ffc500;
  font-size: 12px;
  text-align: center;
  line-height: 20px;
}
</style>